<template>
  <div>
    <div class="row no-gutters widget-2 shadow-base">
      <div class="col-sm-12 col-lg-12">
        <div class="card">
          <div class="card-header bd-b-color-gray-lighter">
            <h6 class="card-title">
              <i class="ion-ios-filing-outline tx-24 va-m"></i> 카테고리 관리
            </h6>
          </div>
          <div class="card-body">
            <div class="row tx-center">
              <div class="col-lg-4">
                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0">
                  <h6 class="tx-14 tx-info tx-left">
                    <i class="ion-android-arrow-dropright-circle tx-14"></i>
                    카테고리
                  </h6>
                </div>
               
                <div class="d-flex mg-b-5">
                    <div class="pd-r-5"><input type="text" class="form-control form-input form-control-sm"/></div>
                    <div class="pd-r-5 btn-group-xs"><button type="button" class="btn btn-outline-danger">폴더추가 </button></div>
                </div>
                <div id="treeview1" class="treeview">
                  <ul class="list-group">
                    <li v-for="item in listItems" v-bind:key="item.id" class="list-group-item rounded-top-0 tx-left" v-on:click="fnOnClickNode(item)" v-bind:class="['data-level-'+item.level, item.isOpen?'open':'closed', item.isShow?'show':'no']">
                    <p class="mg-b-0">
                        <i class="fa fa-check tx-success mg-r-8" v-show="item.isSelected"></i>
                        <i class="ion-android-folder tx-folder mg-r-8"></i>
                        <strong class="tx-inverse tx-medium">{{item.title}}</strong> 
                    </li>
                    
                  </ul>
                </div>
              </div>

              <div class="col-lg-8">
                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0">
                  <h6 class="tx-14 tx-info tx-left">
                    <i class="ion-android-arrow-dropright-circle tx-14"></i>
                    파일목록
                  </h6>
                </div>
                <div class="d-flex mg-b-5">
                    <div class="pd-r-5 form-inline">
                      <select type="text" class="form-control form-select-sm mg-r-5">
                        <option>전체</option>
                      </select>
                       <input type="text" class="form-control form-input form-control-sm"/>
                      
                      </div>
                    <div class="pd-r-5 btn-group-xs"><button type="button" class="btn btn-outline-danger">파일추가 </button></div>
                </div>
                <div>
                  <table class="table table-bordered table-mid tx-13 tx-gray-700 bd">
                      <thead>
                       <tr class="bg-gray-100 tx-11 tx-uppercase tx-gray-800">
                           <th class="wd-40p tx-center">프로젝트</th>
                           <th class="wd-30p tx-center">파일</th>
                           <th class="wd-10p tx-center">파일타입</th>
                           <th class="wd-20p tx-center">등록일</th>
                       </tr>
                     </thead>

                  </table>
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
        selectedItem:null,
        listItems:[
            {id:'01',title:'항만 BIM Browser',sort:10000,level:0,parent:0, isOpen:false,isShow:true}
            ,{id:'02',title:'건축',sort:999,level:1,parent:'01', isOpen:false,isShow:false}
            ,{id:'03',title:'설계도면',sort:998,level:2,parent:'02', isOpen:false,isShow:false}
            ,{id:'04',title:'토목',sort:997,level:1,parent:'01', isOpen:false,isShow:false}
            ,{id:'05',title:'설계도면',sort:996,level:2,parent:'04', isOpen:false,isShow:false}
            ,{id:'06',title:'NNNN',sort:995,level:3,parent:'05', isOpen:false,isShow:false}
            ,{id:'07',title:'07',sort:994,level:4,parent:'06', isOpen:false,isShow:false}
            ,{id:'08',title:'설계도면',sort:993,level:1,parent:'01', isOpen:false,isShow:false}
            ,{id:'09',title:'09',sort:992,level:2,parent:'08', isOpen:false,isShow:false}
            ,{id:'010',title:'전기설비',sort:991,level:1,parent:'01', isOpen:false,isShow:false}
            ,{id:'011',title:'기계설비',sort:990,level:1,parent:'01', isOpen:false,isShow:false}
            ,{id:'012',title:'012',sort:989,level:2,parent:'011', isOpen:false,isShow:false}
            ,{id:'013',title:'013',sort:987,level:3,parent:'012', isOpen:false,isShow:false}

            ]
    };
  },
  created: function () {
    // console.log("created");
  
  },
  mounted: function () {},
  methods: {
      fnOnClickNode:function(item){
        this.selectedItem=item;
        item.isOpen=!item.isOpen;

          this.listItems.forEach(element => {
                element.isSelected=false;
        
            });
        
        item.isSelected=true;
        if(item.isOpen){
            this.fnFolderOpen(item);
        }else{
            this.fnFolderClose(item);
        }
      },
      fnFolderOpen:function(item){

          this.listItems.forEach(element => {
                if(element.parent===item.id){
                element.isShow=true;
                }
            });
        

      },
      fnFolderClose:function(item){
          let preLevel=item.level+1;
          for(let i=0;i<this.listItems.length;i++){
              let _target=this.listItems[i];
            
             if(_target.sort<item.sort){
                  if(_target.level<preLevel) break;
                _target.isShow=false;
                _target.isOpen=false;
              }
          }
        
        

      }

  },
};
</script>


