import 'es6-promise/auto'
import Vue from 'vue';
import VueRouter from 'vue-router';
import Dashboard from './components/dashboard.vue';
import CodeList from './components/code/codeList.vue';
import UserList from './components/user/userList.vue';
import UserLogList from './components/user/userLogList.vue';
import UserLogDetail from './components/user/userLogDetail.vue';
import BbsList from './components/board/bbsList.vue';
import BbsContentList from './components/board/bbsContentList.vue';
import BbsContentWrite from './components/board/bbsContentWrite.vue';
import BbsContentUpdate from './components/board/bbsContentUpdate.vue';
import BbsContentDetail from './components/board/bbsContentDetail.vue';
import BbsContentReply from './components/board/bbsContentReply.vue';
import MenuList from './components/menu/menuList.vue';
import MenuRoleMng from './components/menu/menuRoleMng.vue';
import MenuLogList from './components/menu/menuLogList.vue';
import OrgTree from './components/orgTree.vue';
import CompPager from './components/paging.vue';
import Axios from "axios";
import StringPrototype from '../uitls/string-prototypes.js'
Vue.use(VueRouter);
Vue.component('pager', CompPager);


// 요청 인터셉터 추가
Axios.interceptors.request.use(
    function (config) {
      // 요청을 보내기 전에 수행할 일
      config.headers.Authorization = localStorage.getItem("jtocken");
      config.url=domain+config.url;
      console.log(config);
      return config;
    },
    function (error) {
      // 오류 요청을 보내기전 수행할 일
      // ...
      console.log(error);
      return Promise.reject(error);
    });
  
  // 응답 인터셉터 추가
  Axios.interceptors.response.use(
    function (response) {
        console.log(response);
        let _data=response.data;
        if(_data.errorCode!=undefined && _data.errorCode===9201){
        //  window.location='/src/web-base/html/login.html';
        }
      // 응답 데이터를 가공
      // ...
      return response;
    },
    function (error) {
        console.log(error);
      // 오류 응답을 처리
      // ...
      return Promise.reject(error);
    });

const routes = [    
    { path: '/', component: Dashboard, name: 'dashboard' },
    { path: '/manage/code', component: CodeList, name: 'codeList' },
    { path: '/manage/user', component: UserList, name: 'userList' },
    { path: '/userLogDetail/:userID/:dateS/:dateE', component: UserLogDetail, name: 'userLogDetail' },
    { path: '/userLogList', component: UserLogList, name: 'userLogList' },
    { path: '/manage/board', component: BbsList, name: 'bbsList' },
    { path: '/manage/bbsContents/:bbsID?', component: BbsContentList, name: 'bbsContentList' },
    { path: '/manage/board/contentWrite/:bbsID?', component: BbsContentWrite, name: 'bbsContentWrite' },
    { path: '/manage/org/tree', component: OrgTree, name: 'orgTree' },
    { path: '/bbsContentUpdate/:bbsID/:seq', component: BbsContentUpdate, name: 'bbsContentUpdate' },
    { path: '/bbsContentDetail/:bbsID/:seq', component: BbsContentDetail, name: 'bbsContentDetail' },
    { path: '/bbsContentReply/:bbsID/:seq', component: BbsContentReply, name: 'bbsContentReply'},
    { path: '/manage/menu', component: MenuList, name: 'menuList' },
    { path: '/manage/menuRole', component: MenuRoleMng, name: 'menuRoleMng' },
    { path: '/menuLogList', component: MenuLogList, name: 'menuLogList' }
]

const router = new VueRouter({
    routes
})

const app = new Vue({
    router
}).$mount('#app')



